.camera-switch-btn {
    position: absolute;
    top: 16px;
    right: 16px;
    background: rgba(0, 0, 0, 0.5);
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 1000;
    
    .material-symbols-outlined {
      color: white;
      font-size: 24px;
    }
  
    &:hover {
      background: rgba(0, 0, 0, 0.7);
    }
  }
  